.footerContainer {
  width: 100%;
  height: 30vh;
  background-color: #5fc7e7;
}

.smallerContainer {
  width: 100%;
  height: 100%;
  max-width: 1440px;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex-wrap: wrap;
  margin: auto;
}

.opening,
.location,
.contactInfo {
  line-height: 30px;
  color: white;
}

.opening h4,
.location h4,
.contactInfo h4 {
  font-size: 20px;
}

@media (max-width: 800px) {
  .smallerContainer {
    flex-wrap: wrap;
    column-gap: 10px;
    row-gap: 20px;
    padding: 40px;
    align-items: flex-start;
    justify-content: space-between;
  }
}
